.login-main{
    width:100%;
    height:100%;
    overflow: hidden;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    background-color:#00897B;
    position: relative;
    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}
.login-panel{
    position: absolute;
    bottom:48%;
    width:22rem;
    min-width:19rem;
    margin:auto;
    background-color: #fff;
    -webkit-transition: all ease .3s;
    -o-transition: all ease .3s;
    transition: all ease .3s;
    z-index:2;
    -webkit-box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.22);
            box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.22)
}
.login-form-body{
    padding:20px;
}
.login-label-container{
    display: block;
    margin:30px 0;
    text-align: left;
    position: relative;
}
.login-input{
    padding-left:5px;
    font-size:15px;
    padding-bottom:10px;
    width:100%;
    color: #999;
}
.login-input:focus+.login-bottom-line:after{
    background: #ff7fab;
    height:2px;
}
.login-input-valid:focus+.login-bottom-line:after{
    background: #1fe8b5;
    height:2px;
}
.login-bottom-line:after{
    content: '';
    display: block;
    position: absolute;
    width:200%;
    height:1px;
    bottom:0;
    left: -50%;
    -webkit-transform: scale(0.5);
        -ms-transform: scale(0.5);
            transform: scale(0.5);
    background: #999;
    -webkit-transition: background ease .3s;
    -o-transition: background ease .3s;
    transition: background ease .3s;
}
.login-form-footer{
    width:100%;
    background: #455A64;
    cursor: pointer;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    -webkit-transition: background ease .3s;
    -o-transition: background ease .3s;
    transition: background ease .3s;
}
.login-form-footer:hover{
    background-color: rgba(60,90,100,.9);
}
.login-submit{
    width:100%;
    height:100%;
    font-size:20px;
    color: #fff;
    padding:25px;
    cursor: pointer;
}
.login-forget{
    color: #000;
}
.login-forget:hover{
    font-weight:600;
}
.login-copyright{
    width:100%;
    text-align: center;
    overflow: hidden;
    color: #fff;
    font-size:14px;
    position: absolute;
    bottom:0;
    left: 0;
    padding:30px 0;
    -webkit-box-sizing:border-box;
            box-sizing:border-box;
}
.login-footer-item{
    margin-top:10px;
}


